body {
  background-image: url("navy-blue-background.jpg");
  background-repeat: repeat;
  font-family: Arial, Helvetica, sans-serif;
}

.App {

}

button {
  font-size: 1.2em;
}

.Button-logo {
  position: relative;
  left: 80%;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  margin-left: 20%;
  margin-top: 12%;
  display: block;
  height: 500px;
  float: left;
  background-color: #2d2e60;
}

.Info {
  margin-left: 1%;
  margin-top: 12%;
  display: block;
  width: 40%;
  height: 500px;
  overflow: auto;
  float: left;
  background-color: #34486b;
  color: white;
  padding: 20px;
}

.Site-links {
  text-align: left;
  width: 100%;
  height: 50px;
  display: block;
  background-color: #2d2e60;
  border: 2px solid #2d2e60;
  color: white;
}

.Site-links:hover {
  background-color: #232451;
}

.Social-logo {
  width: 50%;
  height: 50px;
  background-color: #2d2e60;
  border: 2px solid #2d2e60;
}

.Social-logo:hover {
  background-color: #232451;
}

img {
  display: block;
  width: auto !important; /*override the width below*/
  width: 100%;
  max-width: 250px;
  max-height: 60%;
  float: left;
  clear: both;
}

img.social {
  float: none;
  margin-left: auto;
  margin-right: auto;
}

